.container {
    position: relative;
  }
  
  .select {
    transition: 0.2s;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    width: 200px;
    border-width: 1px;
    border-radius: 8px;
    border-color: #cbd5e0;
    appearance: none;
    box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
  
    &:hover {
      scale: 1.15;
      transition: 0.2s;
      cursor: pointer;
    }
  }
  
  .arrow {
    color: #4a5568;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    display: flex;
    align-items: center;
    pointer-events: none;
  
    & svg {
      width: 1rem;
      fill: rgb(18, 12, 93);
      height: 1rem;
    }
  }