.wave {
    margin-top: calc(50vh - 30px);
    margin-left: auto;
    margin-right: auto;
    position: relative;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    opacity: 1;
  
    &:before,
    &:after {
      content: "";
      position: absolute;
      border-radius: 50%;
      border: 4px #fff solid;
      width: 100%;
      height: 100%;
      left: 0px;
    }
  
    &:before {
      animation: wave-outer 0.6s infinite linear;
      transform: scale(1);
      opacity: 1;
    }
  
    &:after {
      animation: wave-inner 0.6s infinite linear;
      transform: scale(0);
      opacity: 0;
    }
  }
  
  @keyframes wave-outer {
    from {
      opacity: 1;
      transform: scale(1);
    }
    to {
      opacity: 0;
      transform: scale(1.5);
    }
  }
  
  @keyframes wave-inner {
    from {
      opacity: 0;
      transform: scale(0.5);
    }
    to {
      opacity: 1;
      transform: scale(1);
    }
  }